import styled from 'styled-components'

import Colors from '../../styles/colors'
import { darken } from 'polished'

export const Container = styled.div`
   display: flex;
   align-items: flex-start;
   justify-content: space-between;

   @media (min-width: 1020px) {
      width: 100%;
   }

   width: 95%;
   max-width: 1020px;
   margin: 0 auto;
   padding-top: 24px;
   min-height: calc(100vh - 64px);
`

export const ExamsList = styled.ul`
   @media (min-width: 1020px) {
      grid-gap: 24px;
      grid-template-columns: repeat(2, 1fr);
   }

   display: grid;
   grid-gap: 12px;
   grid-template-columns: repeat(1, 1fr);
   list-style: none;
`

export const ExamContainer = styled.li`
   display: flex;
   flex-direction: column;
   justify-content: space-between;

   padding: 15px 20px;
   border: 1px solid #e5e5e5;
   border-radius: 4px;
   box-shadow: 0px 1px 3px 1px #c9c9c9;
   background: #ffffff;

   strong {
      border-bottom: 3px solid
         ${props => (props.exam === 1 ? Colors.enade : Colors.poscomp)};
   }

   a {
      overflow: hidden;
      margin-top: auto;
      background: ${props =>
         props.exam === 1 ? Colors.enade : Colors.poscomp};
      padding: 10px;
      text-align: center;
      color: #ffffff;
      border: 0;
      border-radius: 4px;

      transition: background 0.35s;
      &:hover {
         background: ${props =>
            darken(0.25, props.exam === 1 ? Colors.enade : Colors.poscomp)};
      }
   }
`

export const ImagesContainer = styled.div`
   display: flex;
   align-items: center;
   justify-content: space-between;
   height: 80px;
   margin-bottom: 50px;

   img:first-child {
      width: 81px;
      height: 48px;
   }
`

export const TextContainer = styled.div`
   display: flex;
   flex-direction: column;

   strong {
      width: 65%;
      font-size: 28px;
      color: #010101;

      margin-bottom: 25px;
      padding-bottom: 5px;
   }

   p {
      font-size: 16px;
      color: #3f3f3f;
      text-align: justify;
      margin-bottom: 25px;
   }
`

