import React, { Component } from 'react'
import { Link } from 'react-router-dom'

import LogoPET from '../../assets/images/logo_petcc.svg'
import LogoSBC from '../../assets/images/logo_sbc.png'
import LogoINEP from '../../assets/images/logo_inep_small.png'

import {
   Container,
   ExamsList,
   ExamContainer,
   ImagesContainer,
   TextContainer,
   MoreDetailsLink,
} from './styles'

export default class Home extends Component {
   state = {
      exams: [
         {
            id: 1,
            link: 'enade',
            name: 'ENADE',
            logo: LogoINEP,
            description:
               'O Exame Nacional de Desempenho dos Estudantes (Enade) avalia o rendimento dos concluintes dos cursos de graduação em relação aos conteúdos programáticos previstos nas diretrizes curriculares dos cursos, o desenvolvimento de competências e habilidades necessárias ao aprofundamento da formação geral e profissional, e o nível de atualização dos estudantes com relação à realidade brasileira e mundial.',
         },
         {
            id: 2,
            link: 'poscomp',
            name: 'POSCOMP',
            logo: LogoSBC,
            description:
               'O POSCOMP foi concebido para permitir que os candidatos a cursar Programas de Pós-Graduação em Computação possam participar dos processos seletivos em vários Programas no país, sem a necessidade de deslocamento para a sede de cada um dos Programas postulados pelo candidato, cumprindo assim um forte papel de inclusão social.',
         },
      ],
   }

   componentDidMount() {
      document.title = 'Home Page'
   }

   render() {
      const { exams } = this.state

      return (
         <Container>
            <ExamsList>
               {exams.map(exam => (
                  <ExamContainer exam={exam.id}>
                     <ImagesContainer>
                        <img src={LogoPET} alt="PET/CC Logo" />
                        <img src={exam.logo} alt="INEP Logo" />
                     </ImagesContainer>
                     <TextContainer>
                        <strong>{exam.name}</strong>
                        <p>{exam.description}</p>
                     </TextContainer>
                     <Link to={`/${exam.link}`}>Mais Detalhes</Link>
                  </ExamContainer>
               ))}
            </ExamsList>
         </Container>
      )
   }
}
