import React from 'react'
import { Switch, Route } from 'react-router-dom'

import Sim from './pages/Sim'
import Home from './pages/Home'
import Exam from './pages/Exam'
import Exams from './pages/Exams'
import Admin from './pages/Admin'
import Login from './pages/Login'
import Question from './pages/Question'
import Questions from './pages/Questions'
import Dashboard from './pages/Dashboard'
import RegisterExam from './pages/RegisterExam'

export default function Routes() {
   return (
      <Switch>
         <Route path="/" exact component={Home} />
         <Route path="/admin" component={Admin} />
         <Route path="/login" component={Login} />
         <Route path="/dashboard/prova/cadastrar" component={RegisterExam} />
         <Route path="/dashboard" component={Dashboard} />
         <Route path="/:exam/questoes/:year/:question" component={Question} />
         <Route path="/:exam/questoes/:year" component={Questions} />
         <Route path="/:exam/provas" component={Exams} />
         <Route path="/:exam" component={Exam} />
      </Switch>
   )
}
