import React, { Component } from 'react'
import Select from 'react-select'
import { toast } from 'react-toastify'

import api from '../../services/api'

import { FaLongArrowAltRight, FaRegWindowClose } from 'react-icons/fa'
import {
   Container,
   YearInput,
   StatementContainer,
   InputStatementContainer,
   PreviewStatementContainer,
   HeaderContainer,
   AddButton,
   InputStatementList,
   InputStatementItem,
   StatementInput,
   StatementTypeInput,
   StatementPreviewList,
   Statement,
   AlternativesList,
   AlternativeItem,
   AlternativeContent,
   AddQuestionButton,
} from './styles'

const exam_options = [
   { value: 'enade', label: 'ENADE' },
   { value: 'poscomp', label: 'POSCOMP' },
]

const year_options = [
   { value: '2004', label: '2004' },
   { value: '2005', label: '2005' },
   { value: '2006', label: '2006' },
   { value: '2007', label: '2007' },
   { value: '2008', label: '2008' },
   { value: '2009', label: '2009' },
   { value: '2010', label: '2010' },
   { value: '2011', label: '2011' },
   { value: '2012', label: '2012' },
   { value: '2013', label: '2013' },
   { value: '2014', label: '2014' },
   { value: '2015', label: '2015' },
   { value: '2016', label: '2016' },
   { value: '2017', label: '2017' },
   { value: '2018', label: '2018' },
   { value: '2019', label: '2019' },
]

const type_options = [
   { value: 1, label: 'Normal' },
   { value: 2, label: 'Centralizado' },
   { value: 3, label: 'Imagem' },
]

export default class Admin extends Component {
   state = {
      exam: '',
      year_input: '',
      question_number: '',
      statement: [
         {
            id: 0,
            type: 1,
            content: '',
         },
      ],
      alternatives: [
         {
            content: '',
            letter: 'A',
         },
         {
            content: '',
            letter: 'B',
         },
         {
            content: '',
            letter: 'C',
         },
         {
            content: '',
            letter: 'D',
         },
         {
            content: '',
            letter: 'E',
         },
      ],
   }

   handleExamInputChange = option => {
      this.setState({ exam: option.value })
   }

   handleYearInputChange = option => {
      this.setState({ year_input: option.value })
   }

   handleQuestionNumberChange = event => {
      this.setState({ question_number: event.target.value })
   }

   handleAddStatement = event => {
      const { statement } = this.state

      this.setState({
         statement: [
            ...statement,
            { id: statement.length, type: 1, content: '' },
         ],
      })
   }

   handleStatementInputChange = (event, id) => {
      const { statement } = this.state

      const index = statement.findIndex(s => s.id === id)
      statement[index].content = event.target.value

      this.setState({ statement })
   }

   handleTypeInputChange = (option, id) => {
      const { statement } = this.state

      const index = statement.findIndex(s => s.id === id)
      statement[index].type = option.value

      this.setState({ statement })
   }

   handleAlternativesInputChange = (event, letter) => {
      const { alternatives } = this.state

      const index = alternatives.findIndex(a => a.letter === letter)
      alternatives[index].content = event.target.value

      this.setState({ alternatives })
   }

   handleAddQuestion = async () => {
      const {
         exam,
         year_input,
         question_number,
         statement,
         alternatives,
      } = this.state

      if (!year_input && !question_number) {
         console.log('Year and Question Number should not be empty')
         return
      }

      console.log('Adding New Question')
      console.log(`Ano: ${year_input} - Questão: ${question_number}`)
      console.log(statement)
      console.log(alternatives)

      const id = String(
         (Number(year_input) - 2004) * 70 + Number(question_number)
      )

      const response = await api.put(`/exams/${exam}/questions/${id}.json`, {
         id,
         number: question_number,
         anwser: '',
         area: '',
         justification: '',
         year: year_input,
         statement,
         alternatives,
      })

      console.log(response)
      if (response.status === 200) {
         toast.success('Questão adicionada/atualizada com sucesso!')

         this.setState({
            year_input: '',
            question_number: '',
            statement: [
               {
                  id: 0,
                  type: 1,
                  content: '',
               },
            ],
            alternatives: [
               {
                  content: '',
                  letter: 'A',
               },
               {
                  content: '',
                  letter: 'B',
               },
               {
                  content: '',
                  letter: 'C',
               },
               {
                  content: '',
                  letter: 'D',
               },
               {
                  content: '',
                  letter: 'E',
               },
            ],
         })
      }
   }

   render() {
      const { statement, alternatives, question_number } = this.state

      return (
         <Container>
            <span>Prova</span>
            <Select
               options={exam_options}
               isSearchable={false}
               onChange={this.handleExamInputChange}
            />
            <span>Ano da Prova</span>
            <Select
               options={year_options}
               isSearchable={false}
               onChange={this.handleYearInputChange}
            />
            <span>Questão</span>
            <YearInput
               value={question_number}
               onChange={this.handleQuestionNumberChange}
            />
            <StatementContainer>
               <InputStatementContainer>
                  <HeaderContainer>
                     <h4>Enunciado</h4>
                     <AddButton onClick={this.handleAddStatement}>
                        Adicionar
                     </AddButton>
                  </HeaderContainer>
                  <InputStatementList>
                     {statement.map(s => (
                        <InputStatementItem key={s.id}>
                           <Select
                              options={type_options}
                              isSearchable={false}
                              onChange={option =>
                                 this.handleTypeInputChange(option, s.id)
                              }
                           />
                           <StatementInput
                              value={s.content}
                              onChange={event =>
                                 this.handleStatementInputChange(event, s.id)
                              }
                           />
                        </InputStatementItem>
                     ))}
                  </InputStatementList>
               </InputStatementContainer>
               <PreviewStatementContainer>
                  <HeaderContainer>
                     <h4>Preview</h4>
                  </HeaderContainer>
                  <StatementPreviewList>
                     {statement.map(s => (
                        <Statement
                           key={s.id}
                           type={s.type}
                           dangerouslySetInnerHTML={{
                              __html: s.content,
                           }}
                        />
                     ))}
                  </StatementPreviewList>
               </PreviewStatementContainer>
            </StatementContainer>
            <StatementContainer>
               <InputStatementContainer>
                  <HeaderContainer>
                     <h4>Alternativas</h4>
                  </HeaderContainer>
                  <InputStatementList>
                     {alternatives.map(a => (
                        <InputStatementItem key={a.letter}>
                           <strong>{a.letter})</strong>
                           <StatementInput
                              value={a.content}
                              onChange={event =>
                                 this.handleAlternativesInputChange(
                                    event,
                                    a.letter
                                 )
                              }
                           />
                        </InputStatementItem>
                     ))}
                  </InputStatementList>
               </InputStatementContainer>
               <PreviewStatementContainer>
                  <HeaderContainer>
                     <h4>Preview</h4>
                  </HeaderContainer>
                  <AlternativesList>
                     {alternatives.map(a => (
                        <AlternativeItem key={a.letter}>
                           <strong>{a.letter})</strong>
                           <AlternativeContent
                              dangerouslySetInnerHTML={{
                                 __html: a.content,
                              }}
                           />
                        </AlternativeItem>
                     ))}
                  </AlternativesList>
               </PreviewStatementContainer>
            </StatementContainer>
            <AddQuestionButton onClick={this.handleAddQuestion}>
               Adicionar Questão
            </AddQuestionButton>
         </Container>
      )
   }
}
