import React, { Component } from 'react'
import { toast } from 'react-toastify'

import login_api from '../../services/LoginApi'
import history from '../../services/history'

import { FaUser, FaLock } from 'react-icons/fa'
import {
   Container,
   LoginContainer,
   InputContainer,
   ButtonsContainer,
   ForgetPass,
   LoginButton,
} from './styles'

export default class Login extends Component {
   state = {
      mail: '',
      pass: '',
   }

   handleChangeMail = event => {
      this.setState({ mail: event.target.value })
   }

   handleChangePass = event => {
      this.setState({ pass: event.target.value })
   }

   handleLogin = async () => {
      const { mail, pass } = this.state
      if (!mail || !pass) {
         toast.error('Login ou senha devem ser preenchidos!')
         return
      }

      var response = ''
      try {
         response = await login_api.post('', {
            email: mail,
            password: pass,
         })
      } catch (err) {
         toast.error('Login ou senha incorretos!')
         return
      }

      if (response.status === 200) {
         toast.success('Login realizado com sucesso!')
         history.push('/dashboard')
      }
   }

   render() {
      const { mail, pass } = this.state

      return (
         <Container>
            <LoginContainer>
               <span>Acesse com seu e-mail e senha</span>
               <InputContainer>
                  <FaUser />
                  <input
                     type="email"
                     placeholder="Digite seu e-mail"
                     value={mail}
                     onChange={this.handleChangeMail}
                  />
               </InputContainer>
               <InputContainer>
                  <FaLock />
                  <input
                     type="password"
                     placeholder="Digite sua senha"
                     value={pass}
                     onChange={this.handleChangePass}
                  />
               </InputContainer>
               <ButtonsContainer>
                  <ForgetPass to="/">Esqueceu sua senha?</ForgetPass>
                  <LoginButton onClick={this.handleLogin}>Entrar</LoginButton>
               </ButtonsContainer>
            </LoginContainer>
         </Container>
      )
   }
}
