import React, { Component } from 'react'
import Select from 'react-select'
import { FaArrowLeft, FaCheck } from 'react-icons/fa'

import { Container, Header, Form } from './styles'

export default class RegisterExam extends Component {
   state = {
      exam: '',
      exams_options: [
         { value: 'enade', label: 'ENADE' },
         { value: 'poscomp', label: 'POSCOMP' },
      ],
   }

   handleExamInputChange = option => {
      this.setState({ exam: option.value })
   }

   render() {
      const { exams_options } = this.state

      return (
         <Container>
            <Header>
               <span>Cadastro de Prova</span>
               <div>
                  <button>
                     <FaArrowLeft />
                     Voltar
                  </button>
                  <button>
                     <FaCheck />
                     Salvar
                  </button>
               </div>
            </Header>
            <Form>
               <div>
                  <span>Prova</span>
                  <Select
                     options={exams_options}
                     isSearchable={false}
                     onChange={this.handleExamInputChange}
                  />
               </div>
               <div>
                  <span>Ano</span>
                  <input type="number" />
               </div>
            </Form>
         </Container>
      )
   }
}
